@import url(https://fonts.googleapis.com/css?family=PT+Sans+Narrow&display=swap);
html,
body {
  margin: 0;
  padding: 0;
}
body {
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f9e8d094;
}

* {
  font-family: "PT Sans Narrow", sans-serif;
}
.home {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
.dog-icon-img {
  width: 250px;
  height: 200px;
}
.dog-breed-title {
  padding: 60px;
  text-align: center;
  text-transform: uppercase;
  font-size: 50px;
}

.start {
  font-size: 25px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 40px;
  padding-right: 40px;
  cursor: pointer;
  border: 2px solid black;
  background-color: orange;
}

.App {
  margin: 20px;
}

.image-container {
  padding: 20px;
  margin: 0;
  text-align: center;
}

.image-container img {
  width: 250px;
  height: 250px;
}
img .image-card {
  max-width: 100%;
  max-height: 100%;
}

.plaintext {
  font-size: 20px;
}

.header {
  text-align: center;
  margin-bottom: 32px;
  padding: 16px 0;
  width: 100%;

  font-size: 30px;
}
.select {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
}
.breed-select {
  font-size: 20px;
}

